@import url(https://fonts.googleapis.com/css?family=Heebo|Roboto);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Roboto", sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.main {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.nav {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.nav__list {
  margin: 2em 0;
  padding: 0;
  list-style: none;
}

.nav__item {
  font-family: "Heebo", sans-serif;
  display: inline-block;
  text-transform: uppercase;
  font-weight: 600;
  margin: 0 1em;
}

.hero-image {
  width: 400px;
  height: 400px;
  background-size: cover;
  background-position: 57% center;
  border-radius: 50%;
}

.character-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.character-item {
  margin: 1em 0;
}

/* Short description */
.char-info--short {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  height: 3em;
}

.char-info__avatar {
  height: 100%;
  border-radius: 3px;
  margin-right: 1em;
}

/* Full description */
.char-info {
  display: -webkit-flex;
  display: flex;
}

.avatar,
.main-info {
  margin: 1em;
}

.description {
  min-width: 250px;
}
.description dt {
  float: left;
  clear: left;
  width: 25%;
  text-align: right;
  margin-right: 0.5em;
}

.description dd {
  float: left;
  width: 60%;
  padding: 0;
  margin: 0;
}

.paginator {
  margin: 2em;
}

.paginator-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.paginator-item {
  display: inline-block;
}

.page-button {
  cursor: pointer;
  padding: 0.5em 1em;
  margin: 0 0.3em;
  font-family: "Heebo", sans-serif;
  font-weight: 600;
  background: none;
  border: 1px solid #ccc;
  border-radius: 2px;
}

.page-button:disabled {
  background-color: #dcdcdc;
  color: black;
}

.locations-list {
  padding: 0;
  margin: 0;
  list-style: none;
}

.location-item {
  margin: 0.5em 0;
}

.episodes-list {
  padding: 0;
  margin: 0;
  list-style: none;
}

.episode-item {
  margin: 0.5em 0;
}

